body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1{
  font-family:italic;
}
.title{
  height:80px;
  background:rgb(207, 253, 40);
  font-family:Arial, Helvetica, sans-serif;
}
.title span{
  font-size:40px;
}
.header{
  background-color: #D6FF37;
  height: 80px;
  padding: 40px;
}
.headerText{
  font-family:Arial, Helvetica, sans-serif;
  font-size: 50px;
  margin-left: 50px;
}
.main{
  margin-left: 30px;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
h1{
  border-bottom: 1px solid silver;
  font-weight: 700;
  font-size: 45px;
  margin-left: 30px;
  height: 120px;
}

.name{
  font-size: 22px;
  font-weight: 700;
}
.block{
  height: 180px;
  width: 400px;
  border-bottom: 1px solid silver;
  position: relative;
  padding-bottom: 28px; 
}
.hit{
  width: 60px;
  position: absolute;
  top: -30px;
  right: 60px;
  
}
.rouming{
  text-transform: uppercase;
  font-weight: 700;
  color: #7B7C80;
}
.price{
  color: #1F2229;
  font-weight: 650;
  font-size: 60px;
  margin-bottom: 20px;
}
.period{
  font-size: 16px;
  font-weight: 500;
}
.r{
  margin-left: 5px;
  font-size: 25px;
  font-weight: 600;
}
.right{
  display: inline-block;
  margin: 5px;  
  
}
.internetP{
  font-size: 33px;
  font-weight: 600;
}
.gb{
  font-weight: 400;
  font-size: 30px;
}
.unlimP{
  font-size: 20px;
  font-weight: 500;
}
.callMinutes{
  font-size: 30px;
  font-weight: 700;
}
.callMinutesText{
  font-weight: 500;
  font-size: 22px;
}
.unlimMinutes{
  font-weight: 500;
  font-size: 20px;
}
.sms{
  font-weight: 700;
  font-size: 25px;
}
.smsText{
  font-weight: 500;
}
.fee{
  font-size: 18px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}
.socialIcons{
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
}
.icon{
  margin: -3px;
  width: 35px;
}
.btn button{
  float:right;
  font-size:15px;
  width:130px;
  height:50px;
  background: #fff;
  border-radius:50px;
  border:2px solid black;
  font-weight: bold;
}
.btn button:hover{
  background-color: #D6FF37;
}
.settings a{
  color:black;
  font-weight:bold;
}
.settings{
  width:20px;
  margin-top:60px;
  margin-right:-70px;
  float:right;
}